import gql from 'graphql-tag'
import { Organization as GraphOrganization } from '@namcbugdb/common'

import { Api } from '../api'
import { Organization } from '../../types'

export const fetchOrganizationByIdQuery = gql`
  query ($organizationId: Int) {
    organizationsById(ids: [$organizationId]) {
      records {
        organizationName
        organizationTypeId
        entityId
        address1
        address2
        city
        usState
        usStateId
        country
        countryId
        zipCode
        phone
        userCount
      }
    }
  }
`

export const fetchOrganizationById =
  (api: Api) =>
  async (organizationId: number): Promise<Organization | null> => {
    const data = await api.query<{ organizationsById: { records: GraphOrganization[] } }>(fetchOrganizationByIdQuery, {
      organizationId,
    })
    const organization = data.organizationsById.records[0]
    if (!organization) return null
    const { entityId: id, organizationName: name, organizationTypeId: typeId, ...restOrganization } = organization
    return { id, name, typeId, ...restOrganization }
  }
