import gql from 'graphql-tag'
import { Individual as GraphIndividual } from '@namcbugdb/common'
import log from 'loglevel'

import { Api } from '../api'
import { User } from '../../types'

export const fetchUserByIdQuery = gql`
  query ($userId: Int!) {
    user(id: $userId) {
      id
      firstName
      lastName
      email
      phone
      affiliationId
      organizationName
      roleIds
    }
  }
`

export const fetchUserById =
  (api: Api) =>
  async (userId: number): Promise<User | null> => {
    const data = await api.query<{ user: GraphIndividual }>(fetchUserByIdQuery, { userId })
    const { user } = data
    if (!user) return null
    const fullName = `${user.firstName} ${user.lastName}`
    return { fullName, ...data.user, roleIds: user.roleIds || [], organizationName: user.organizationName || '' }
  }
